import React from "react";
import "./styles/style.css";
import { ColorSelector, Selector, Execute } from "./colorSelector";

function CmdButton({ cmd, text, value, promptMsg }) {
  const ExecN = () => {
    Execute({ cmd: cmd, value: value || "" });
  };

  const ExePrompt = () => {
    var value_ = prompt(promptMsg);

    Execute({ cmd: cmd, value: value_ });
  };
  return (
    <button title={text} onClick={promptMsg ? ExePrompt : ExecN} className="btn btn-primary">
      {text}
    </button>
  );
}

function SizeBtn() {
  return (
    <select
      className="form-controll"
      ref={(r) => {
        for (let i = 0; i < r.children.length; i++) {
          r.children[i].addEventListener("click", function (e) {
            Execute({ cmd: "fontSize", value: this.value });
          });
        }
      }}
    >
      <option value={4}>medium</option>
      <option value={5}>x-medium</option>
      <option value={6}>large</option>
      <option value={6}>x-large</option>
      <option value={7}>large</option>
    </select>
  );
}

export default class ToolBar extends React.Component {
  render() {
    return (
      <div id="toolBox">
        <span>
          <ColorSelector />
          <br />
          <Selector />
        </span>
        <span>
          <SizeBtn />
          <div className="sizeBtn">Text Size</div>
        </span>

        <span>
          <CmdButton cmd={"bold"} text={"Bold"} value={null} />
        </span>

        <span>
          <CmdButton
            cmd={"createLink"}
            text={"Link"}
            value={null}
            promptMsg={"Enter Url."}
          />
        </span>

        <span>
          <CmdButton
            cmd={"insertOrderedList"}
            text={"OrderList"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"insertUnorderedList"}
            text={"UnorderList"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"justifyCenter"}
            text={"JustifyMid"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"removeFormat"}
            text={"RemoveFmt"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"strikeThrough"}
            text={"strike"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"underline"}
            text={"underline"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton
            cmd={"subscript"}
            text={"subscript"}
            value={null}
            promptMsg={null}
          />
        </span>
        <span>
          <CmdButton
            cmd={"superscript"}
            text={"superscript"}
            value={null}
            promptMsg={null}
          />
        </span>

        <span>
          <CmdButton cmd={"undo"} text={"undo"} value={null} promptMsg={null} />
        </span>

        <span>
          <CmdButton cmd={"JustifyLeft"} text={"Left"} value={null} promptMsg={null} />
        </span>
        <span>
        <CmdButton cmd={"JustifyRight"} text={"Right"} value={null} promptMsg={null} />
      </span>
      
      </div>
    );
  }
}
