import React, { useState } from "react";
import "./styles/style.css";
import "bootstrap/dist/css/bootstrap.css";

import { FilePond } from "react-filepond";
// Import FilePond styles
import "filepond/dist/filepond.min.css";
import { getJSON,post } from "jquery";
/********************/
function Image_({ getResponse }) {
  return (
    <React.Fragment>
      <FilePond
        allowMultiple={false}
        server={"/img/save"}
        onprocessfile={() => {
          getJSON("/tempImg", {}, (data) => {
            getResponse(data.url);
          });
        }}
      />
    </React.Fragment>
  );
}

/*******************/

function NULL_C() {
  return <span></span>;
}

function Controlles({ done, isDone, edit, del, addImg }) {
  return (
    <span id="controlles">
      <button
        onClick={isDone ? edit : done}
        className={isDone ? "btn btn-danger" : "btn btn-success"}
      >
        {isDone && "Edit is OFF"}
        {!isDone && "Edit is ON"}
      </button>
      <button onClick={del} className="btn btn-danger">
        delete
      </button>
      <button onClick={addImg} className="btn btn-info">
        Add Image
      </button>
    </span>
  );
}

function ContentBox({ ref_, isDone, Component, getResponse }) {
  console.log(Component);
  return (
    <React.Fragment>
      <div
        style={{ visibility: Component ? "hidden" : "" }}
        ref={ref_}
        className="contentBox"
        contentEditable={isDone ? false : true}
      >
        Text
      </div>
      {Component && <Component getResponse={getResponse} />}
    </React.Fragment>
  );
}

function EditBox({ del }) {
  const [state, setState] = useState({
    addImg: false,
    done: false,
    component_: Image_,
  });

  var contentBox = null;
  const done = () => {
    setState(Object.assign({}, state, { done: true }));
  };

  const edit = () => {
    setState(Object.assign({}, state, { done: false }));
  };

  const addImage = () => {
    setState({ addImg: true, done: state.done, component_: Image_ });
  };

  const getResponse = function (res) {
    const id = res;

    if (contentBox) {
      const img = document.createElement("img");
      img.src = "http://techserver01.herokuapp.com/img/get/" + id;
      img.className="img-fluid";
      contentBox.appendChild(img);
      setState({ addImg: false });
    }
  };
  return (
    <div id="editBox">
      <Controlles
        del={del}
        done={done}
        isDone={state.done}
        addImg={addImage}
        edit={edit}
      />
      <ContentBox
        ref_={(ref) => {
          contentBox = ref;
        }}
        isDone={state.done}
        getResponse={state.addImg ? getResponse : false}
        Component={state.addImg ? state.component_ : false}
      />
    </div>
  );
}

function Post({ getData }) {
  return (
    <button
      onClick={getData}
      type="button"
      className="form-control btn-primary"
    >
      Post
    </button>
  );
}
function EditorBox({ _ref, components, add, del }) {
  return (
    <div ref={_ref} id="editorBox">
      {components.map((Com, index) => {
        return (
          <Com key={index} add={add} del={((ind_) => () => del(ind_))(index)} />
        );
      })}
    </div>
  );
}

function AddComponent({ add }) {
  return (
    <div onClick={add} className="card-header">
      +
    </div>
  );
}

export default class Editor extends React.Component {
  constructor(props) {
    super(props);
    this.state = { flg: true };
    this.editor = { ref: null };
    this.state.components = [EditBox, AddComponent];
  }

  getData() {
    var editorBox = this.editor.ref;
    var data = [];
    for (var i = 0; i < editorBox.children.length - 1; i++) {
      var editBox = editorBox.children[i];

      var contentBox = editBox.children[1];

      if (contentBox&&contentBox.innerHTML) {
        var html = contentBox.innerHTML;
        var buffer = Buffer.from(html);
        data.push(buffer);
      }
    }

     var blob = Buffer.concat(data);

     var title = prompt("Write the title of your post?");
     var thum  = prompt("Write a link of your thumnail image?");

     post("/auth/create/post",{fileData:blob.toString('utf8'),title:title,thum:thum},function(data){
         
           
    },"text");

     //var text  = blob.toString('utf8');


  }

  add() {
    //{component}

    const temp = this.state.components.pop();

    this.state.components.push(EditBox);

    this.state.components.push(temp);

    this.setState({ flg: !this.state.flg, components: this.state.components });
  }

  del(index) {
    if (index === this.state.components.length - 1) {
      return;
    }
    this.state.components.splice(index, 1, NULL_C);//avoid presetting of index
    this.setState({ flg: !this.state.flg, components: this.state.components });
  }
  render() {
    return (
      <React.Fragment>
        <Post getData={this.getData.bind(this)} />
        <EditorBox
          _ref={(ref) => {
            this.editor.ref = ref;
          }}
          components={this.state.components}
          add={this.add.bind(this)}
          del={this.del.bind(this)}
        />
      </React.Fragment>
    );
  }
}
